import common from "../../common/common.js"
import CutBar from "../../components/colorui/components/cutbar";
import review from "../../components/dl-review/review";
import ChatBar from "../../components/colorui/components/chatbar";
// import ChatBar from "../../components/user-chat/user-chat-bottom";
var serverUrl = common.serverUrl;
export default{
	name:"PostDetail",
	props:{

	},
	components:{
		CutBar,
		review,
		ChatBar
	},
	data() {
			return {
			postId:"",
			isFollow:false,
			cutBarText:"最新评论",
			commentCount: 0,
			// 列表详情数据
			childData: [],
			// 评论列表
			reviewMsg: [],
			userId:"",
			userInfo:{},
			token:'',
			type:'',
			post:{},
			clickComment:{},
			comments:[],
			InputBottom: 0,
			placeHolder:"理一下ta好不好",
			content:'',
			parentId:"",
			isFocus:false,
			commentObj:{}
			}

		},
		onLoad(paramsObj) {
			var userInfo = uni.getStorageSync("globalUser")
			if(userInfo!=null&&userInfo!=""&&userInfo!=undefined){
				this.userInfo=userInfo;
				this.token="Bearer "+this.userInfo.token;
				this.type=this.userInfo.tokenType
				this.userId=this.userInfo.userId
			}else{
				this.isFollow=false
			}
			this.postId = paramsObj.postId
			uni.request({
				url:serverUrl+'/posts/'+this.postId,
				method:'GET',
				success: (res) => {
					if(res.data.code===0){
						var post =res.data.data
						this.post= post
						this.commentCount=post.commentCount
						uni.request({
							header:{
								"Authorization":this.token,
								"type":this.type
							},
							url:serverUrl+'/follows/'+post.userId+'?userId='+this.userId,
							method:'GET',
							success: (res) => {
								if(res.data.code===0){
									this.isFollow=res.data.data

								}
								if(this.post.userId===this.userId&&this.userId!==""){
									this.isFollow=true
								}

							},
							fail: () => {

							},
							complete: () => {

							}
						});
					}
				},
				fail: () => {

				},
				complete: () => {

				}
			});

			uni.request({
				url:serverUrl+'/comments/'+this.postId,
				method:'GET',
				success: res => {
					if(res.data.code===0){
						this.comments=res.data.data
					}

				},
				fail: () => {

				},
				complete: () => {

				}
			});
			uni.request({
				url:"http://192.168.1.7:8086/comments/?apiRootId="+this.postId,
				method:'GET',
				success: (res) => {
					if(res.data.code===0){
						this.reviewMsg=res.data.data.reviewMsg
					}
				}
			})


		},
	methods:{
		sendComment(){
			var comment=this.commentObj
			if(comment.cenId!==null&&comment.cenId!==undefined){
				var parentId=comment.cenId
				var apiRootId=comment.apiRootId
				if(comment.parentId==="0"){
					apiRootId=comment.cenId
				}
			}else{
				debugger
				var apiRootId = this.post.postId
				var parentId = "0"
			}


			uni.request({
				url:serverUrl+'/comments/',
				method:'POST',
				data:{
					"userId":this.userId,
					"content":this.content,
					"type":3,
					"parentId":parentId,
					"apiRootId":apiRootId
				},
				success: (res) => {
					if(res.data.code===0){
						this.content=""
						this.placeHolder="理一下ta好不好"
						uni.request({
							url:"http://192.168.1.7:8086/comments/?apiRootId="+this.postId,
							method:'GET',
							success: (res) => {
								if(res.data.code===0){
									this.reviewMsg=res.data.data.reviewMsg
								}
							}
						})
					}
				}

			})
		},
		contentInput(e){
			var value= e.detail.value
			this.content=value
		},
		commentForChildren(data){
			this.placeHolder='@'+data.userName
			this.isFocus=!this.isFocus
			this.commentObj=data
		},
		childReview(data) {
			this.clickComment=data
			uni.request({
				url:"http://192.168.1.7:8086/comments/"+data.cenId,
				method:'GET',
				success: (res) => {
					if(res.data.code===0){
						this.childData=res.data.data
					}
				}
			})
		},
		follow(){
			var idolId=this.post.userId
			uni.request({
				header:{
					"Authorization":this.token,
					"type":this.type
				},
				url:serverUrl+'/follows/',
				method: 'POST',
				data:{
					"userId":this.userId,
					"idolId":idolId
				},
				success: (res) => {
					if(res.data.code===123){
						uni.showToast({title:res.data.msg,duration:1500})
						this.isFollow=true
					}else if (res.data.code === 30003) {
						uni.showToast({title:res.data.msg,duration:1500})
					}else if (res.data.code === 30002) {
						uni.showToast({title:res.data.msg,duration:1500})
					}else if (res.data.code === 147) {
						uni.showToast({title:res.data.msg,duration:1500})
					}
				},
				complete() {
					// uni.hideNavigationBarLoading()

				}
			})

		},
		love(type){
			this.$emit("love",{
				type,
				index:this.index
				})
		},
		goToPostDetail(){
			uni.navigateTo({
				url:"./PostDetail"
			})

		},
		// follow(index){
		// 	// 点击后触发一个事件给父组件
		// 	this.$emit('follow',this.post.userId)
		// },
		openSpace(){
			uni.navigateTo({
				url:"../userCenter/userCenter"
			})
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		}



	}

}

